import React, { useState, useEffect, useRef } from 'react';
import { Modal, Button, Form, Row, Col, Card } from 'react-bootstrap';
import '../../assets/styles/settingsform.css';
import FormGroupControl from './FormGroupControl';
import defaultSettings from '../../utils/settings';
import { getAllSettings, getSetting, saveSetting, deleteSetting, exportSettings, importSettings} from '../../utils/idb';
import { FaChevronDown, FaFileExport, FaFileImport } from 'react-icons/fa';
import JSZip from 'jszip';

const SettingsForm = ({ show, handleClose, saveSettings }) => {
const [settingsList, setSettingsList] = useState([]);
const [currentSetting, setCurrentSetting] = useState('');
const [settingName, setSettingName] = useState('');
const [proxyUrl, setProxyUrl] = useState(defaultSettings.proxyUrl);
const [websiteUrl, setWebsiteUrl] = useState(defaultSettings.websiteUrl);
const [guideInitId, setGuideInitId] = useState(defaultSettings.guideInitId);
const [companyName, setCompanyName] = useState(defaultSettings.companyName);
const [skillId, setSkillId] = useState(defaultSettings.skillId);
const [customIvaJson, setCustomIvaJson] = useState(defaultSettings.customIvaJson);
const [botId, setBotId] = useState(defaultSettings.botId);
const [guideStyles, setGuideStyles] = useState(defaultSettings.guideStyles);
const [chatStyles, setChatStyles] = useState(defaultSettings.chatStyles);
const [isSettingNameReadOnly, setIsSettingNameReadOnly] = useState(false);
const [hidePreSurvey, setHidePreSurvey] = useState(defaultSettings.hidePreSurvey);
const [errors, setErrors] = useState({});
const [showSettingsSelection, setShowSettingsSelection] = useState(true);
const fileInputRef = useRef(null);


const setSettingsState = (settings) => {
    setSettingName(settings.settingName || '');
    setProxyUrl(settings.proxyUrl || '');
    setWebsiteUrl(settings.websiteUrl || '');
    setGuideInitId(settings.guideInitId || '');
    setCompanyName(settings.companyName || '');
    setSkillId(settings.skillId || '');
    setCustomIvaJson(settings.customIvaJson || '');
    setBotId(settings.botId || '');
    setGuideStyles(settings.guideStyles || '');
    setChatStyles(settings.chatStyles || '');
    setHidePreSurvey(settings.hidePreSurvey || false);
  };
  
  const resetToDefaultSettings = () => {
    setSettingName('');
    setProxyUrl(defaultSettings.proxyUrl);
    setWebsiteUrl(defaultSettings.websiteUrl);
    setGuideInitId(defaultSettings.guideInitId);
    setCompanyName(defaultSettings.companyName);
    setSkillId(defaultSettings.skillId);
    setCustomIvaJson(defaultSettings.customIvaJson);
    setBotId(defaultSettings.botId);
    setGuideStyles(defaultSettings.guideStyles);
    setChatStyles(defaultSettings.chatStyles);
    setHidePreSurvey(defaultSettings.hidePreSurvey);
  };

  useEffect(() => {
    const loadSettings = async () => {
        const allSettings = await getAllSettings();
        setSettingsList(allSettings.filter(key => key !== 'current-setting'));
        const currentSetting = await getSetting('current-setting');
        if (currentSetting) {
            setCurrentSetting(currentSetting);
            const settings = await getSetting(currentSetting);
            if (settings) {
                setSettingsState({ ...settings, settingName: currentSetting });
                setIsSettingNameReadOnly(true);
            }
        }
    };
    if (show) {
      loadSettings();
      setShowSettingsSelection(true);
      setErrors({});
    }
  }, [show]);

  const handleSave = async () => {
    const newErrors = {};
    if (!settingName.trim()) {
        newErrors.settingName = 'Setting Name is required.';
    }
    else if (!isSettingNameReadOnly && settingsList.includes(settingName.trim())) {
        newErrors.settingName = 'Setting Name already exists. Please choose a different name.';
    }
    const urlPattern = /^(https?|ftp):\/\/[^\s/$.?#].[^\s]*$/i;
    if (!proxyUrl.trim() || !urlPattern.test(proxyUrl)) {
        newErrors.proxyUrl = 'Proxy URL is required and must be a valid URL.';
    }
    if (!websiteUrl.trim() || !urlPattern.test(websiteUrl)) {
        newErrors.websiteUrl = 'Website URL is required and must be a valid URL.';
    }
    if (!guideInitId.trim() || guideInitId.length !== 36) {
        newErrors.guideInitId = 'Guide Init ID is required and must be exactly 36 characters long.';
    }    
    if (Object.keys(newErrors).length > 0) {
        setErrors(newErrors);
        return;
    }
    const settings = {
        proxyUrl: proxyUrl.trim(),
        websiteUrl: websiteUrl.trim(),
        guideInitId: guideInitId.trim(),
        companyName: companyName.trim(),
        skillId: skillId.trim(),
        customIvaJson: customIvaJson.trim(),
        botId: botId.trim(),
        guideStyles: guideStyles.trim(),
        chatStyles: chatStyles.trim(),
        hidePreSurvey
    };
    await saveSetting(settingName, settings);
    await saveSetting('current-setting', settingName);
    saveSettings(settings);
    setShowSettingsSelection(true);
    handleClose();
    window.location.reload();
  };

  const handleAddNew = () => {
    resetToDefaultSettings();
    setIsSettingNameReadOnly(false);
    setShowSettingsSelection(false);
  };

  const handleSelectSetting = async (e) => {
    const selectedSetting = e.target.value;
    setCurrentSetting(selectedSetting);
    const settings = await getSetting(selectedSetting);
    if (settings) {
        setSettingsState({ ...settings, settingName: selectedSetting });
        setIsSettingNameReadOnly(true);
    }
  };

  const handleDeleteSetting = async () => {
    await deleteSetting(currentSetting);
    const updatedSettingsList = settingsList.filter(setting => setting !== currentSetting);
    setSettingsList(updatedSettingsList);
  
    if (updatedSettingsList.length > 0) {
      const nextSetting = updatedSettingsList[0];
      setCurrentSetting(nextSetting);
      const settings = await getSetting(nextSetting);
      if (settings) {
        setSettingsState({ ...settings, settingName: nextSetting });
        setIsSettingNameReadOnly(true);
      }
    } else {
      await deleteSetting('current-setting');
      setCurrentSetting(null);
      resetToDefaultSettings();
      setIsSettingNameReadOnly(false);
    }
  };  

  const handleExport = async () => {
    const allData = await exportSettings();
    const blob = new Blob([JSON.stringify(allData)], { type: 'application/json' });
    const zip = new JSZip();
    zip.file('NICEviewSettings.json', blob);
    const content = await zip.generateAsync({ type: 'blob' });
    const url = URL.createObjectURL(content);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'NICEviewSettings.zip';
    a.click();
    URL.revokeObjectURL(url);
  };    

  const handleImport = async (event) => {
    const file = event.target.files[0];
    if (!file) return;
  
    const reader = new FileReader();
    reader.onload = async (e) => {
      const zip = await JSZip.loadAsync(e.target.result);
      const jsonFile = zip.file('NICEviewSettings.json');
      if (jsonFile) {
        const data = JSON.parse(await jsonFile.async('text'));
        await importSettings(data);
  
        // Set current-setting to the first entry in the imported data
        const firstSetting = data.find(item => item.key !== 'current-setting');
        if (firstSetting) {
          await saveSetting('current-setting', firstSetting.key);
          setCurrentSetting(firstSetting.key);
        }
  
        // Reload settings list after import
        const allSettings = await getAllSettings();
        setSettingsList(allSettings.filter(key => key !== 'current-setting'));
      }
    };
    reader.readAsArrayBuffer(file);
  };
    

  return (
    <Modal show={show} onHide={handleClose} size="lg" className="centered-modal">
      <Modal.Header className="d-flex justify-content-between align-items-center" style={{ width: '100%' }}>
        <Button variant="secondary" onClick={() => fileInputRef.current.click()}><FaFileImport /> Import</Button>
        <Modal.Title className="flex-grow-1 text-center">Settings</Modal.Title>
        <Button variant="secondary" onClick={handleExport}><FaFileExport /> Export</Button>
        <button type="button" className="btn-close" aria-label="Close" onClick={handleClose}></button>
      </Modal.Header>
      <Modal.Body>
        <Form>
          {showSettingsSelection && settingsList.length > 0 && (
            <Form.Group controlId="formSelectSetting" className="mb-3">
                <Form.Label className="w-100" style={{ textAlign: 'center' }}>Select Setting</Form.Label>
                <div className="d-flex align-items-center">
                <Button variant="danger" onClick={handleDeleteSetting} className="me-2" style={{ width: '35px', height: '35px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                    -
                </Button>
                <div className="position-relative flex-grow-1 mx-2">
                    <Form.Control as="select" value={currentSetting} onChange={handleSelectSetting} className="w-100" style={{ height: '35px', paddingRight: '30px' }}>
                    {settingsList.map((setting, index) => (
                        <option key={index} value={setting}>
                        {setting}
                        </option>
                    ))}
                    </Form.Control>
                    <FaChevronDown className="position-absolute" style={{ right: '10px', top: '50%', transform: 'translateY(-50%)', pointerEvents: 'none' }} />
                </div>
                <Button variant="secondary" onClick={handleAddNew} className="ms-2" style={{ width: '35px', height: '35px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                    +
                </Button>
                </div>
            </Form.Group>                                       
          )}
          <Card className="mt-3">
            <Card.Body>
            <FormGroupControl
                controlId="formSettingName"
                label="Setting Name"
                value={settingName}
                onChange={(e) => setSettingName(e.target.value)}
                readOnly={isSettingNameReadOnly}
                error={errors.settingName}
                />
              <Row>
                <Col md={6}>
                <FormGroupControl
                    controlId="formProxyUrl"
                    label="Proxy URL"
                    value={proxyUrl}
                    onChange={(e) => setProxyUrl(e.target.value)}
                    error={errors.proxyUrl}
                    />
                </Col>
                <Col md={6}>
                <FormGroupControl
                    controlId="formWebsiteUrl"
                    label="Website URL"
                    value={websiteUrl}
                    onChange={(e) => setWebsiteUrl(e.target.value)}
                    error={errors.websiteUrl}
                    />
                </Col>
              </Row>
              <Row>
                <Col md={6}>
                <FormGroupControl
                    controlId="formGuideInitId"
                    label="Guide Init ID"
                    value={guideInitId}
                    onChange={(e) => setGuideInitId(e.target.value)}
                    error={errors.guideInitId}
                    />
                </Col>
                <Col md={6}>
                <FormGroupControl
                    controlId="formCompanyName"
                    label="Company Name"
                    value={companyName}
                    onChange={(e) => setCompanyName(e.target.value)}
                    />
                </Col>
              </Row>
              <Row>
                <Col md={6}>
                <FormGroupControl
                    controlId="formSkillId"
                    label="Skill ID"
                    value={skillId}
                    onChange={(e) => setSkillId(e.target.value)}
                    />
                </Col>
                <Col md={6}>
                <FormGroupControl
                    controlId="formBotId"
                    label="Bot Id"
                    value={botId}
                    onChange={(e) => setBotId(e.target.value)}
                    />                
                </Col>
              </Row>
              <Row>
                <Col md={6}>
                <FormGroupControl
                    controlId="formCustomIvaJson"
                    label="Custom IVA JSON"
                    value={customIvaJson}
                    onChange={(e) => setCustomIvaJson(e.target.value)}
                    as="textarea"
                    />
                </Col>
                <Col md={6}>
                <FormGroupControl
                    controlId="formGuideStyles"
                    label="Guide Styles"
                    value={guideStyles}
                    onChange={(e) => setGuideStyles(e.target.value)}
                    as="textarea"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col md={6}>
                  <FormGroupControl
                        controlId="formChatStyles"
                        label="Chat Styles"
                        value={chatStyles}
                        onChange={(e) => setChatStyles(e.target.value)}
                        as="textarea"
                        />
                  </Col>
                  <Col md={6}>
                  <FormGroupControl
                        controlId="formHidePreSurvey"
                        label="Hide Pre-Survey"
                        value={hidePreSurvey}
                        onChange={(e) => setHidePreSurvey(e.target.checked)}
                        as="checkbox"
                        />
                  </Col>
                </Row>
              </Card.Body>
            </Card>
          </Form>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-center">
            <Button variant="secondary" onClick={handleClose}>
                Close
            </Button>
            <Button variant="primary" onClick={handleSave} className="ms-2">
                Save
            </Button>
        </Modal.Footer>
        <input
        type="file"
        ref={fileInputRef}
        style={{ display: 'none' }}
        onChange={handleImport} />
      </Modal>
    );
  };
  
  export default SettingsForm; 