// FormGroupControl.js
import React from 'react';
import { Form } from 'react-bootstrap';

const FormGroupControl = ({ controlId, label, value, onChange, readOnly = false, as = "input", rows = 3, error }) => {
  if (as === "checkbox") {
    return (
      <Form.Group controlId={controlId} className="mb-3">
        <Form.Label>{label}</Form.Label>
        <Form.Check
          type="checkbox"
          checked={value}
          onChange={onChange}
          readOnly={readOnly}
          className="ms-2"
        />
        {error && <Form.Text className="text-danger">{error}</Form.Text>}
      </Form.Group>
    );
  }

  return (
    <Form.Group controlId={controlId} className="mb-3">
      <Form.Label>{label}</Form.Label>
      <Form.Control
        as={as}
        rows={rows}
        type="text"
        value={value}
        onChange={onChange}
        className="w-100"
        readOnly={readOnly}
        isInvalid={!!error}
      />
      <Form.Control.Feedback type="invalid">
        {error}
      </Form.Control.Feedback>
    </Form.Group>
  );
};

export default FormGroupControl;